
.perfrume-story-container .heading {
    font-weight: 600;
    line-height: 3.281rem;
    color: #000;
    text-transform: uppercase;
    font-size: 1.8rem;
    letter-spacing: .028rem;
}

.perfrume-story-container .right-content {
    /* width: 70%; */
    font-size: 1.2rem;
}

.perfrume-story-container .left-content {
    font-size: 1.2rem;
    /* margin-top: 2rem; */
}

.perfrume-story-container .pic-wrapper {
    padding:5px;
}

.perfrume-story-container .author-name {
    display: flex;
    justify-content: flex-end;
    color:#000;
}

.hr{
    height:2px;
    background-color: #000;
    margin:20px;
    /* padding-left:10px; */
}


.pic-wrapper img{
    border-radius:10px;
    max-height:400px;
}

.right-content .title, .left-content .title{
    padding-left:0px;
    margin-bottom:10px;
}

.right-content p, .left-content p{
    color:#000;
}

.perfrume-story-container a:hover{
    text-decoration: none;
    color:#000;
}

@media screen and (max-width:576px) {
    .perfrume-story-container .right-content {
        width: 100%;
    }
}